$text:#02abb2; // 주석
$back:white;
*{background-color: $back;
color: $text;}
.Header{ z-index: 12;
    // background-color: $text;ba
    position: fixed;
    top: 0;
    color: $text;
    width: 100vw;
    height: 8vh;
display: grid;
grid-template-columns:35vw 60vw;
align-items: center;
div{
    font-size: 2.5vw;
    font-weight: 700;
    // background-color: $text;

    display: flex;
    justify-content:space-around;
    color: $back;
}

.menu{
    margin-left: 10vw;
    padding-left: 5vw;
    justify-content: space-evenly;
    li{
        font-size:1.8vw;
        list-style:none;
    }
}
    li{  
        // background-color: $text;
        list-style:none;
        
    }
}