
$text: #02abb2; // 주석

.contactWrap{
    width: 100vw;
}

img{
width: 80%;
height: 40vh;}
.imga{
    padding-left:10vw ;
    height: 80vh;
margin-top: 10vh;}
.contactBody{
    
    display: flex;
    flex-direction: column;   
    align-items: center; 
    margin-top: 10vh;
    padding-left: 10vw;
    padding-right: 10vw;
    .inputbody{
        row-gap: 5vh;
        width: 100%;
        height: 115vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        
        
    }
    .description{
        justify-self: center;
        width:50vw;
        height: 20vh;
        font-size: 3vh;
        border-color: rgba(0, 0, 0,0.1);

    }
    ::placeholder{
        color: black;
        font-size: 3vh;

    }
}
.input_trigger{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    row-gap: 1vh;
    input{
        border-color: rgba(0, 0, 0,0.1);
        color:black;
        width: 50vw;
        font-size: 3vh;
    }}
    button{
        align-self: center;
        margin-left: 33vw;
        font-size:3vh;
        width: 18vw;        
        margin-top: 1vh;
        color:black;
        border-color: rgba(0, 0, 0,0.1);
    }
    .kakao{
        width: 20vw;
        height: 15vh;
        margin-left: 35vw;            

        img{
            width: 20vw;
            height: 15vh;
        }
    }